import React, { Component } from 'react'
import { Route } from "react-router-dom";
import { AT4 } from '../components/AT - Form/AT4';
import { AT8 } from '../components/AT - Form/AT8';
import { AT5 } from '../components/AT - Form/AT5';
import { AT7 } from '../components/AT - Form/AT7';
export class ATAdd extends Component {
  render() {
    return (
      <div>
        <Route
          path={process.env.PUBLIC_URL + "/AT4"}
          exact={true}
          component={AT4}
        />
         <Route
          path={process.env.PUBLIC_URL + "/AT8"}
          exact={true}
          component={AT8}
        />
         <Route
          path={process.env.PUBLIC_URL + "/AT5"}
          exact={true}
          component={AT5}
        />
         <Route
          path={process.env.PUBLIC_URL + "/AT7"}
          exact={true}
          component={AT7}
        />
      </div>
    )
  }
}

export default ATAdd
